@import '../app/appBasicStyleSetting';

.app-cartProduct {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  justify-content: space-around;

  form, div, p {
    margin: 0; } }

.app-cartProduct--title {
  flex: 3; }

.app-cartProduct--unitPrice {
  flex: 2; }

.app-cartProduct--subTotal {
  flex: 5; }

.app-cartProduct--qty {
  flex: 3;

  input[type="number"] {
    font-size: 24px; } }

@media screen and (min-width: 768px) {
  .app-cartProduct {
    flex-direction: row;
    align-items: center; }

  .app-cartProduct--qty {
    input[type="number"] {
      font-size: 14px; } } }
